.MainView{
    /* background-color: #282c34; */
    background-color: transparent;
    padding: 30px;
    color: black;
}
.Cover{
    float: left;
    width: 100%;
    text-align: center;
}
.Cover img{
    max-width: 100%;
    box-shadow: 5px 5px 5px #888888;
}
.Title{
    float: left;
    width: 100%;
    text-align: center;
    font-weight: bold;
    font-size: 30px;
}
.Artist{
    float: left;
    width: 100%;
    text-align: center;
}
.MainView .Action{
    float: left;
    width: 100%;
    text-align: center;
}
.MainView .Action button{
    width: 100px;
    border-radius: 20px;
}
.Info{
    text-align: center
}
.normalTrack{
    border-bottom: 1px solid lightgray;
    line-height: 50px;
    height: 50px;
    overflow: hidden;
}
.normalTrack:hover{
    animation: TrackListRowHover 1s;
    animation-fill-mode: forwards;
    cursor: pointer;
}
.activeTrack{
    background-color: gray;
    line-height: 50px;
    height: 50px;
}
@keyframes TrackListRowHover{
    0%{background-color: white;}
    100%{background-color: lightgray; transform: translateX(10px);}
}
.MainView ol{
    list-style: none;
}