body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
*{
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Container{
  height: 100%;
}

.activeLink{  
  font-weight: bold;
}

#root{
  height: 100%;
}
.NavBar{
    position: relative;
}
.NavBar a{
    text-decoration: none;
    color: gray;
}
.NavBar a:hover{
    text-decoration: none;
    font-weight: bold;
    color: gray;
}
.NavHeader{
    float: left;
    width: 100%;
}
.NavHeader span{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 30px;
    font-weight: bold;
}
.NavContent{
    float: left;
    width: 100%;
    padding: 30px;
}
.RecentlyPlayed{
    float: left;
    width: 100%;
    padding: 30px;
    color: gray;
}
.NavFooter{
    float: left;
    width: 100%;
    padding: 30px;
}
.NavBar .NavHeader img{
    position: relative;
    width: 75px;
    height: auto;
}
ul{
    list-style: none;
}
li{
    display: list-item;
}
.NavBarGroup{
    height: 30px;
    line-height: 30px;
}
.NavItem{
    line-height: 30px;
    letter-spacing: .015em;
    font-weight: var(--glue-font-weight-bold);
}
.NavItem span{
    margin-left: 5px;
    line-height: 30px;
}
.NavItemIcon{
    float: left;
}
.NavItemIcon img{
    width: 20px;
}
.Action span{
    margin-left: 5px; 
}
.Avatar{
    width: 50px;
    height: 50px;
    border-radius: 25px;
    margin: 5px;
}
.RecentlyPlayedItem{
    border-bottom: 2px dotted lightgray;
    font-size: 12px;
}
.activeLink{
    color: red;
}
.NowPlayingBar{
    padding: 10px;
    background-color: white;
    box-shadow: -5px -5px 5px #888888;
    position: fixed;
    bottom: 0;
    width: 100%;
}
.NowPlayingBar a{
    text-decoration: none;
    color: black;
}
.NowPlayingBar a:hover{
    text-decoration: underline;
    color: black;
}
.NowPlayingBar-Left .TrackImage img{
    max-width: 100%;
}
.NowPlayingBar-Left .TrackInfo{
    overflow: hidden;
}
.NowPlayingBar-Left .TrackAction img{
    max-width: 100%;
}
.center{
    text-align: center;
}
.NowPlayingBar-Right img{
    margin: 5px;
    max-width: 50%;
}
.small-icon{
    width: 24px;
    margin: 5px;
}
.middle{
    line-height: 100%;
}
.MainView{
    /* background-color: #282c34; */
    background-color: transparent;
    padding: 30px;
    color: black;
}
.Cover{
    float: left;
    width: 100%;
    text-align: center;
}
.Cover img{
    max-width: 100%;
    box-shadow: 5px 5px 5px #888888;
}
.Title{
    float: left;
    width: 100%;
    text-align: center;
    font-weight: bold;
    font-size: 30px;
}
.Artist{
    float: left;
    width: 100%;
    text-align: center;
}
.MainView .Action{
    float: left;
    width: 100%;
    text-align: center;
}
.MainView .Action button{
    width: 100px;
    border-radius: 20px;
}
.Info{
    text-align: center
}
.normalTrack{
    border-bottom: 1px solid lightgray;
    line-height: 50px;
    height: 50px;
    overflow: hidden;
}
.normalTrack:hover{
    -webkit-animation: TrackListRowHover 1s;
            animation: TrackListRowHover 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    cursor: pointer;
}
.activeTrack{
    background-color: gray;
    line-height: 50px;
    height: 50px;
}
@-webkit-keyframes TrackListRowHover{
    0%{background-color: white;}
    100%{background-color: lightgray; -webkit-transform: translateX(10px); transform: translateX(10px);}
}
@keyframes TrackListRowHover{
    0%{background-color: white;}
    100%{background-color: lightgray; -webkit-transform: translateX(10px); transform: translateX(10px);}
}
.MainView ol{
    list-style: none;
}
.album{
    width: 100%;
    height: 100%;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
}

.album .col-md-2{
    padding-left: 5px !important;
    padding-right: 5px !important;
}

.album h1{
    margin-top: 10px;
    padding-bottom: 10px;
}

.album .card h6{
    font-size: 12px;
    color: black;
}

.album .card p {
    color: #989898
}

.album .card{
    font-size: 11px;
    text-align: center;
    box-shadow: 0px 10px 15px gray;
    margin-bottom: 10px;    
    border: none;
}

.album .card:hover{
    cursor: pointer;
    -webkit-animation: cardHover 1s forwards;
            animation: cardHover 1s forwards;
}

.album .card .card-img-top{
    width: 100%;
    min-height: 150px;
}

.album .card .card-body{
    width: 100%;
    min-height: 75px;
    overflow: hidden;
}

@-webkit-keyframes cardHover{
    0%{}
    100%{box-shadow: none;}
}

@keyframes cardHover{
    0%{}
    100%{box-shadow: none;}
}
.trackTitle{
    overflow: hidden;
}

.search-result{
    text-align: left;
    margin-bottom: 200px;
}

.search-result h3{
    text-shadow: 2px 2px 2px lightblue;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: bold;
    color: black;
}

.search-result .album{
    padding: 5px;
    margin: 10px;
    text-align: center;
    box-shadow: 0px 10px 15px gray;
    min-height: 270px;
}

.search-result .album:hover{
    -webkit-animation: albumHover 1s forwards;
            animation: albumHover 1s forwards;
    cursor: pointer;
}

.search-result .row .album img{
    margin-top: 5px; 
    width: 100%;
}

.search-result .row .album span{
    overflow: hidden;
}

.search-track .normalTrack{
    border: 0;
}

@-webkit-keyframes albumHover{
    0%{}
    100%{box-shadow: none;}
}

@keyframes albumHover{
    0%{}
    100%{box-shadow: none;}
}
