.NavBar{
    position: relative;
}
.NavBar a{
    text-decoration: none;
    color: gray;
}
.NavBar a:hover{
    text-decoration: none;
    font-weight: bold;
    color: gray;
}
.NavHeader{
    float: left;
    width: 100%;
}
.NavHeader span{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 30px;
    font-weight: bold;
}
.NavContent{
    float: left;
    width: 100%;
    padding: 30px;
}
.RecentlyPlayed{
    float: left;
    width: 100%;
    padding: 30px;
    color: gray;
}
.NavFooter{
    float: left;
    width: 100%;
    padding: 30px;
}
.NavBar .NavHeader img{
    position: relative;
    width: 75px;
    height: auto;
}
ul{
    list-style: none;
}
li{
    display: list-item;
}
.NavBarGroup{
    height: 30px;
    line-height: 30px;
}
.NavItem{
    line-height: 30px;
    letter-spacing: .015em;
    font-weight: var(--glue-font-weight-bold);
}
.NavItem span{
    margin-left: 5px;
    line-height: 30px;
}
.NavItemIcon{
    float: left;
}
.NavItemIcon img{
    width: 20px;
}
.Action span{
    margin-left: 5px; 
}
.Avatar{
    width: 50px;
    height: 50px;
    border-radius: 25px;
    margin: 5px;
}
.RecentlyPlayedItem{
    border-bottom: 2px dotted lightgray;
    font-size: 12px;
}
.activeLink{
    color: red;
}