.album{
    width: 100%;
    height: 100%;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
}

.album .col-md-2{
    padding-left: 5px !important;
    padding-right: 5px !important;
}

.album h1{
    margin-top: 10px;
    padding-bottom: 10px;
}

.album .card h6{
    font-size: 12px;
    color: black;
}

.album .card p {
    color: #989898
}

.album .card{
    font-size: 11px;
    text-align: center;
    box-shadow: 0px 10px 15px gray;
    margin-bottom: 10px;    
    border: none;
}

.album .card:hover{
    cursor: pointer;
    animation: cardHover 1s forwards;
}

.album .card .card-img-top{
    width: 100%;
    min-height: 150px;
}

.album .card .card-body{
    width: 100%;
    min-height: 75px;
    overflow: hidden;
}

@keyframes cardHover{
    0%{}
    100%{box-shadow: none;}
}