.trackTitle{
    overflow: hidden;
}

.search-result{
    text-align: left;
    margin-bottom: 200px;
}

.search-result h3{
    text-shadow: 2px 2px 2px lightblue;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: bold;
    color: black;
}

.search-result .album{
    padding: 5px;
    margin: 10px;
    text-align: center;
    box-shadow: 0px 10px 15px gray;
    min-height: 270px;
}

.search-result .album:hover{
    animation: albumHover 1s forwards;
    cursor: pointer;
}

.search-result .row .album img{
    margin-top: 5px; 
    width: 100%;
}

.search-result .row .album span{
    overflow: hidden;
}

.search-track .normalTrack{
    border: 0;
}

@keyframes albumHover{
    0%{}
    100%{box-shadow: none;}
}