.NowPlayingBar{
    padding: 10px;
    background-color: white;
    box-shadow: -5px -5px 5px #888888;
    position: fixed;
    bottom: 0;
    width: 100%;
}
.NowPlayingBar a{
    text-decoration: none;
    color: black;
}
.NowPlayingBar a:hover{
    text-decoration: underline;
    color: black;
}
.NowPlayingBar-Left .TrackImage img{
    max-width: 100%;
}
.NowPlayingBar-Left .TrackInfo{
    overflow: hidden;
}
.NowPlayingBar-Left .TrackAction img{
    max-width: 100%;
}
.center{
    text-align: center;
}
.NowPlayingBar-Right img{
    margin: 5px;
    max-width: 50%;
}
.small-icon{
    width: 24px;
    margin: 5px;
}
.middle{
    line-height: 100%;
}